<template>
  <div>
    <DataFilter @ChangeValue="FilterChange" />
    <BuyerList :list="DataList" />
  </div>
</template>

<script>
import BuyerList from '@/components/BuyerList.vue'
import DataFilter from '@/components/DataFilter.vue'

export default {
  components: {
    BuyerList,
    DataFilter,
  },
  data() {
    return {
      list: [],
      filterType: '',
      filterValue: '',
    }
  },
  computed: {
    DataList() {
      let state = this.$route.name
      let dataList = []
      switch (state) {
        case 'Active':
          dataList = this.list.filter(item => {
            return item.data.complete === false
          })
          break
        case 'Complete':
          dataList = this.list.filter(item => {
            return item.data.complete === true
          })
          break
        case 'All':
          dataList = this.list
      }

      if (this.filterValue !== '') {
        dataList = dataList.filter(item => {
          return item['data'][this.filterType] === this.filterValue
        })
      }

      return dataList
    },
  },
  methods: {
    FilterChange(value) {
      this.filterType = value.filterType
      this.filterValue = value.filterValue

      this.list[1]['data']['filterType']
    },
  },
  mounted() {
    this.list = this.$store.getters.DataList
  },
}
</script>

<style lang="scss">
h2 {
  padding: 20px;
  font-size: 1.8rem;
}
</style>
