<template>
  <li class="item">
    <button
      class="button-check"
      :class="{ complete: data.complete }"
      @click="Complete"
    >
      <font-awesome-icon icon="check" v-if="data.complete" />
    </button>
    <span class="item-text">
      {{ data.name }}<br />
      {{ Total }}<br />
      {{ Day }}
    </span>
    <button class="button-edit" @click="Edit">
      <font-awesome-icon icon="edit" />
    </button>
    <button class="button-delete" @click="Delete">
      <font-awesome-icon icon="trash" />
    </button>
  </li>
</template>

<script>
export default {
  props: {
    id: '',
    data: {},
  },
  computed: {
    Day() {
      let date = new Date(this.data.date)
      let week = ['日', '一', '二', '三', '四', '五', '六']
      return `${date.getFullYear() - 1911}年${date.getMonth() +
        1}月${date.getDate()}日  星期${week[date.getDay()]}`
    },
    Total() {
      let string = ''
      if (this.data.amount !== 0) string += this.data.amount + '尾'
      if (this.data.real) string += '實'
      if (this.data.amount !== 0 && this.data.bag !== 0) string += ' 加 '
      if (this.data.bag !== 0) string += this.data.bag + '袋'

      return string
    },
  },
  methods: {
    Complete() {
      if (!this.data.complete) {
        this.data.complete = true
      } else {
        this.data.complete = false
      }
      let payload = { id: this.id, data: this.data }
      this.$store.dispatch('UpdateData', payload)
    },
    Delete() {
      let check = window.confirm('確定要刪除這筆訂單?')
      if (check) {
        this.$store.dispatch('DeleteData', this.id)
      } else {
        /*do nothing */
      }
    },
    Edit() {
      this.$router.push({ name: 'Edit', query: { id: this.id } })
    },
  },
}
</script>

<style lang="scss">
.list-item {
  min-width: 360px * 0.9;
  width: 90%;
  display: flex;
  align-items: center;
  padding: 10px 5px;
  margin: 0 auto;
  border-bottom: #9d9d9d 1px solid;
  box-sizing: border-box;

  button {
    width: 30px;
    height: 30px;
    margin-right: 10px;
    border-radius: 999em;
    border: none;
    outline: none;
    cursor: pointer;
    position: relative;
    font-size: 1.3rem;
    flex-shrink: 0;
    transition: 0.5s;

    svg {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  .button-check {
    background-color: #f0f0f0;
  }

  .complete {
    background-color: #ffcc00;
  }

  .button-edit,
  .button-delete {
    background-color: transparent;
  }

  span {
    display: inline-block;
    width: 75%;
    min-width: 180px;
    padding: 0 20px;
    line-height: 1.2;
  }
}
</style>
