import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/active',
    name: 'Active',
    component: Home,
  },
  {
    path: '/complete',
    name: 'Complete',
    component: Home,
  },
  {
    path: '/all',
    name: 'All',
    component: Home,
  },
  {
    path: '/edit/',
    name: 'Edit',
    component: () => import(/* webpackChunkName: "edit" */ '../views/Edit.vue'),
  },
  {
    path: '*',
    redirect: 'active',
  },
]

const router = new VueRouter({
  routes,
  linkActiveClass: 'active',
})

export default router
