import firebase from 'firebase/app'
import 'firebase/firestore'

// Your web app's Firebase configuration
var firebaseConfig = {
  apiKey: process.env.VUE_APP_FIRESTORE_API_KEY,
  authDomain: process.env.VUE_APP_FIRESTORE_AUTH,
  databaseURL: process.env.VUE_APP_FIRESTORE_DATABASEURL,
  projectId: process.env.VUE_APP_FIRESTORE_PROJECT_ID,
}
// Initialize Firebase
export const database = firebase.initializeApp(firebaseConfig).firestore()
