import Vue from 'vue'
import Vuex from 'vuex'
import { database } from '@/firestore'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    list: [],
  },
  getters: {
    DataList: state => {
      return state.list
    },
    DataIndex: state => id => {
      return state.list.findIndex(element => {
        return element.id === id
      })
    },
    FindData: (state, getters) => id => {
      return state.list[getters.DataIndex(id)].data
    },
  },
  mutations: {
    Set_Data(state, data) {
      state.list.push(data)
      state.list.sort((a, b) => {
        return a.data.date - b.data.date
      })
    },
    Update_Data(state, payload) {
      state.list.splice(this.getters.DataIndex(payload.id), 1, payload)
    },
    Delete_Data(state, id) {
      state.list.splice(this.getters.DataIndex(id), 1)
    },
  },
  actions: {
    GetData(context) {
      database
        .collection('sales')
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            let data = doc.data()
            let item = {
              id: doc.id,
              data: data,
            }
            context.commit('Set_Data', item)
          })
        })
    },
    AddData(context, payload) {
      database
        .collection('sales')
        .add(payload)
        .then(docRef => {
          let tmp = {}
          tmp = payload
          tmp.id = docRef.id
          context.commit('Set_Data', tmp)
        })
        .catch(err => {
          console.log(err)
        })
    },
    UpdateData(context, payload) {
      database
        .collection('sales')
        .doc(payload.id)
        .set(payload.data)
        .then(() => {
          context.commit('Update_Data', payload)
        })
        .catch(err => {
          console.log(err)
        })
    },
    DeleteData(context, id) {
      database
        .collection('sales')
        .doc(id)
        .delete()
        .then(() => {
          context.commit('Delete_Data', id)
        })
    },
  },
  modules: {},
})
