<template>
  <div>
    <select name="filter-select" id="" v-model="filterType" @change="Value">
      <option value="name">姓名</option>
      <option value="date">日期</option>
    </select>

    <input
      type="text"
      name="filter-value"
      id=""
      v-model="filterValue"
      @change="Value"
    />
  </div>
</template>

<script>
export default {
  data() {
    return {
      filterType: 'name',
      filterValue: '',
    }
  },
  methods: {
    Value() {
      let value = {
        filterType: this.filterType,
        filterValue: this.filterValue,
      }
      this.$emit('ChangeValue', value)
    },
  },
}
</script>

