<template>
  <header class="block sunny">
    <div class="menu">
      <input type="checkbox" name="menu-checkbox" id="menu-checkbox" />
      <label for="menu-checkbox" class="hamburger-menu">
        <span class="icon-bar"></span>
        <span class="icon-bar"></span>
        <span class="icon-bar"></span>
      </label>
      <nav>
        <router-link :to="{ name: 'Active' }">
          <font-awesome-icon icon="clipboard-list" />現有訂單
        </router-link>
        <router-link :to="{ name: 'Complete' }">
          <font-awesome-icon icon="clipboard-check" />已完成
        </router-link>
        <router-link :to="{ name: 'All' }">
          <font-awesome-icon icon="clipboard" />全部訂單
        </router-link>
      </nav>
    </div>
    <h3>{{ DateString }}</h3>
    <button class="add" @click="RouteToAddItem">
      <font-awesome-icon icon="plus" :class="{ edit: isEditing }" />
    </button>
  </header>
</template>

<script>
export default {
  name: 'AppHeader',
  data() {
    return {
      isEditing: false,
    }
  },
  computed: {
    DateString() {
      return new Date().toLocaleDateString()
    },
  },
  methods: {
    RouteToAddItem() {
      if (this.$route.name === 'Edit') {
        this.$router.push({ name: 'Active' }).catch(err => {
          console.log(err)
        })
      } else {
        this.$router
          .push({ name: 'Edit' })
          .then(() => {})
          .catch(err => {
            console.log(err)
          })
      }
    },
  },

  watch: {
    $route(to) {
      if (to.name !== 'Edit') this.isEditing = false
      else this.isEditing = true
    },
  },
}
</script>

<style lang="scss" scope>
header {
  position: relative;
  width: 100%;
  max-width: 768px;
  height: 30vh;
  background-repeat: no-repeat, no-repeat;
  background-position: center center, center center;
  background-size: auto, auto;

  .hamburger-menu {
    display: block;
    width: 30px;
    height: 30px;
    position: relative;
    background-color: transparent;
    cursor: pointer;
  }
  .icon-bar {
    background-color: rgba($color: #fff, $alpha: 0.8);
    position: absolute;
    left: 0;
    height: 2px;
    transition: 0.3s;
  }

  .hamburger-menu .icon-bar:nth-child(1) {
    top: 2px;
    width: 90%;
  }
  .hamburger-menu .icon-bar:nth-child(2) {
    top: 14px;
    width: 60%;
  }
  .hamburger-menu .icon-bar:nth-child(3) {
    top: 26px;
    width: 80%;
  }

  h3 {
    font-size: 1.3rem;
    color: #fff;
    position: absolute;
    bottom: 0;
    left: 0;
    transform: translate(25%, -50%);
  }

  p {
    color: #fff;
  }

  nav {
    padding-top: 10px;

    a {
      width: 100px;
      display: block;
      color: #fff;
      text-decoration: none;
      font-size: 1rem;
      padding: 10px 0px;
      transition: 0.3s ease-in-out;
      transform: translateX(-50px);
      opacity: 0;
      visibility: hidden;

      svg {
        padding-right: 5px;
      }

      &.active {
        color: #ffcc00;
      }
    }

    a:nth-child(1) {
      transition-delay: 0;
    }
    a:nth-child(2) {
      transition-delay: 0.1s;
    }
    a:nth-child(3) {
      transition-delay: 0.2s;
    }
  }
}

.sunny {
  background-image: linear-gradient(
      150deg,
      rgba(0, 128, 225, 0.3),
      rgba(0, 128, 225, 0.3)
    ),
    url('../assets/sunny.jpg');
}

.add {
  width: 50px;
  height: 50px;
  position: absolute;
  right: 5%;
  bottom: 0px;
  transform: translateY(50%);
  background-color: #ff6666;
  border-radius: 999em;
  border: transparent solid 2px;
  outline: none;
  cursor: pointer;
  svg {
    font-size: 1.3rem;
    color: #fff;
    margin: auto;
    transition: 0.3s;
  }
}

.edit {
  transform: rotateZ(135deg);
}

.menu {
  margin-left: 15px;
  padding-top: 15px;

  #menu-checkbox {
    width: 5px;
    height: 5px;
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;

    &:checked ~ .hamburger-menu .icon-bar {
      width: 100%;
    }
    &:checked ~ nav a {
      transform: translateX(0px);
      opacity: 1;
      visibility: visible;
    }
  }
}
</style>
